<template>
  <div class="loginBox">
    <el-button type="success" size="large" @click="openLoginBox"> / </el-button>
    <div class="box" @click="closeLoginBox" v-if="ifShow">
      <div class="main" @click.stop>
        <el-tabs
          v-model="activeName"
        >
          <el-tab-pane label="" name="first">
            <el-form
              :model="loginForm"
              status-icon
              :rules="rules"
              ref="loginForm"
              label-width="80px"
              @keydown.enter="loginSubmit"
            >
              <el-form-item label="" prop="user" label-width="80px">
                <el-input v-model="loginForm.user"/>
              </el-form-item>
              <el-form-item label="" prop="pass" label-width="80px">
                <el-input type="password" v-model="loginForm.pass"/>
              </el-form-item>
              <el-form-item label-width="80px">
                <el-button type="primary" @click="loginSubmit"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-form
              :model="regForm"
              status-icon
              :rules="rules"
              ref="regForm"
              label-width="80px"
              @keydown.enter="regSubmit"
            >
              <el-form-item label="" prop="user" label-width="80px">
                <el-input v-model="regForm.user"/>
              </el-form-item>
              <el-form-item label="" prop="pass" label-width="80px">
                <el-input type="password" v-model="regForm.pass"/>
              </el-form-item>
              <el-form-item label="" prop="pass2" label-width="80px">
                <el-input type="password" v-model="regForm.pass2"/>
              </el-form-item>
              <el-form-item label-width="80px">
                <el-button type="primary" @click="regSubmit"></el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>

</template>

<script>

import {mapMutations} from "vuex"
import Particles from "../Particles/Particles"
export default {
  name: "LoginBox",Particles,
  data() {
    return {
      ifShow: false,
      activeName: "first",

      //
      loginForm: {
        user: "",
        pass: ""
      },
      //
      regForm: {
        user: "",
        pass: "",
        pass2: ""
      },
      //
      rules: {
        user: [
          {required: true, message: ""},
          {
            validator(rule, value, cb) {
              //
              if (value.length < 2 || value.length > 8) {
                cb(new Error("2~8"))
              }
              //
              if (!/^[\w\u4e00-\u9fa5\u0800-\u4e00\uac00-\ud7ff]+$/.test(value)) {
                cb(new Error("   _ //"))
              }

              cb()
            }
          }
        ],
        pass: [
          {required: true, message: ""},
          {
            validator(rule, value, cb) {
              //
              if (value.length < 6 || value.length > 18) {
                cb(new Error("6~18"))
              }
              //
              if (!/^[\w\[\]\/\\~`|<>,.?;':"{}!@#$%^&*()_+=-]+$/.test(value)) {
                cb(new Error(""))
              }

              cb()
            }
          }
        ],
        pass2: [
          {required: true, message: ""},
          {
            validator: (rule, value, cb) => {
              if (this.regForm.pass !== value) {
                cb(new Error(""))
              }
              cb()
            }
          }
        ]
      }
    }
  },

  methods: {
    ///
    openLoginBox() {
      this.ifShow = true
    },
    ///
    closeLoginBox(){
      this.ifShow = false
    },

    //
    regSubmit() {
      this.$refs.regForm.validate(async v => {
        if (v) {
          //
          let {data} = await this.$axios({
            method: "POST",
            url: "/reg",
            data: {
              user: this.regForm.user,
              pass: this.regForm.pass
            }
          })

          //
          if (data.code) {
            this.$message.error(data.msg)
            return
          }

          //
          this.$message.success("")
          //
          this.$refs.regForm.resetFields()
          //
          this.activeName = "first"

        } else {
          return false
        }
      })
    },

    //
    loginSubmit() {
      this.$refs.loginForm.validate(async v=>{
        if (v){
          let {data} = await this.$axios({
            method: "POST",
            url: "/login",
            data: this.loginForm
          })

          //
          if (data.code){
            return this.$message.error(data.msg)
          }

          //
          this.$message.success("") //
          this.$refs.loginForm.resetFields() //
          this.ifShow = false //
          this.loginSuccess(data.data) //vuex

        }else {
          return false
        }
      })
    },

    //vuex
    ...mapMutations(['loginSuccess'])
  }
}
</script>

<style scoped lang="less">
.loginBox{
  padding: 11px 0;
  .box {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background: url("../../assets/img/personal/vor.gif") no-repeat;
    background-size:cover ;

    .main {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 300px;
      padding: 10px 20px;
      background-color: #fff;
    }
  }
}


</style>














