import {createStore} from 'vuex'

export default createStore({
  state: {
    //
    userInfo: {},

    //
    linkData: []
  },
  mutations: {
    //
    loginSuccess(state, userInfo) {
      state.userInfo = userInfo
    },
    //
    loginOut(state) {
      state.userInfo = {}
    },

    //
    updateLinkData(state, data) {
      state.linkData = data
    }
  },
  actions: {

  },
  modules: {}
})
